import React, {Component} from 'react';
import jvrMapLogo from "../img/logo.svg";
import mail_icon from "../img/mail_icon.svg";
import linked_in from "../img/my_linked_in_link.svg";

export default class OpenScreen extends Component {
	render() {

		return (
			<div>
				<div className={"component-container"}>
					<div className={"titleBox-title"}>
						<div className={"row"}>
							<div className={"nameLogo"}>
								<img src={jvrMapLogo} className="jvrMapLogo"
								     alt="Jeff Van Rees Map Logo."/>
							</div>
							<div className={"linkIcons"}>
								<div className="icon-container">
									<a href={"mailto:jvanrees@wisc.edu"}>
										<img src={mail_icon} className="titleIcons"
										     alt="Jeff's e-mail."/>
									</a>
									<a href={"https://www.linkedin.com/in/jeffvanrees/"}>
										<img src={linked_in} className="titleIcons"
										     alt="Jeff's Linked In Profile. Feel free to connect."/>
									</a>
								</div>
							</div>
						</div>

					</div>
					<div className={"titleBox-about-me"}>
						<div className={"titleBox-about-me-content"}>
							<div className={"titleBox-about-me-content subheading"}>
								Hello, I'm Jeff and I make maps on the internet.
							</div>
							<div className={"titleBox-about-me-content supporting-text"}>
								I transform complex questions and data into meaningful and digestible
								interactive visual experiences. Let's build the future of spatial
								together.
							</div>
							{this.props.children}
						</div>
					</div>
				</div>
			</div>
		)
	}
}
