import React, {Component} from 'react';

import {Route, withRouter} from "react-router-dom";

import HomeGrid from './screens/homeGrid.js'
import AboutMeScreen from './screens/AboutMeScreen.js'
import ProjectScreen from './screens/ProjectScreen.js'
import OpacityController from './OpacityController'

class TheRouter extends Component {
	constructor(props) {
		super(props)

		this.state = {
			viewport: {
				latitude: 40.5853,
				longitude: -105.0844,
				zoom: 12,
				minZoom: 2,
				bearing: 0,
				pitch: 0,
				width: 200,
				height: 500
			},
			home: true,
			aboutMe: false,
			rmaHistoryBox: false,
			googleMapsApiBox: false,
			rmaVisitorBox: false,
			qingMapBox: false,
			nightModeBox: false,
			shadedReliefBox: false,
			leafletBox: false,
			hoverInfo: null,
			nightModeMapOpacity: 0,
			currentPage: "/"
		}
	}

	componentDidMount() {
		this.props.getOpenPath(this.props.history.location.pathname)

	}

	componentWillMount() {
		// console.log("The router props",this.props)
		// will trigger the callback function whenever a new Route renders a component(as long as this component stays mounted as routes change)
		this.props.history.listen(() => {
			this.props.getOpenPath(this.props.history.location.pathname)
			// console.log('New URL', this.props.history.location.pathname);
		});
	}

	render() {
		return (

			<div>
				{/*{console.log(this)}*/}
				<Route exact path="/" render={props => <HomeGrid
					nightModeMapOpacity={this.props.nightModeMapOpacity}{...props} />}/>
				<Route path="/aboutme" component={AboutMeScreen}/>
				<Route path="/rma-android" render={props => <ProjectScreen
					project={"RmaAndroid"} {...props} />}/>
				<Route path="/carto-css"
				       render={props => <ProjectScreen project={"CartoCSS"} {...props} />}/>
				<Route path="/google-maps-api"
				       render={props => <ProjectScreen project={"GoogleMaps"} {...props} />}/>
				<Route path="/rma-post-gis"
				       render={props => <ProjectScreen project={"RmaVisitor"} {...props} />}/>
				<Route path="/night-navigation"
				       render={props => <ProjectScreen project={"NightMode"}
				                                       nightModeMapOpacity={this.props.nightModeMapOpacity} {...props} />}/>
				<Route path="/shaded-relief"
				       render={props => <ProjectScreen project={"ShadedRelief"} {...props} />}/>
				<Route path="/leaflet-map"
				       render={props => <ProjectScreen project={"LeafletMap"} {...props} />}/>
				<Route path="/opacity-test"
				       render={props => <OpacityController project={"LeafletMap"} {...props} />}/>
			</div>

		)
	}
}

export default withRouter(TheRouter)
