import React, {Component} from 'react';
import Iframe from 'react-iframe';

export class RmaVisitorMedia extends Component {
	constructor(props) {
		super(props);
		this.state = {
			hasIframeLoaded: false
		}
		this.myRef = React.createRef();

	}

	componentDidMount() {
		// document.getElementById("myId").addEventListener("load", ()=>console.log("iframe load called"));
	}

	render() {

		return (

				<Iframe url="https://gentle-crag-83147.herokuapp.com/"
				        id="myId"
				        className="rma-db-iframe"
				        display="initial"
				        position="relative"
				        allowFullScreen
				        ref={this.myRef}
				        // onLoad={console.log("iframe load called")}
				/>

		)
	}
}

export class RmaVisitorDesc extends Component {
	constructor(props) {
		super(props);
		this.state = {
			statsHeight: "300px",
			didLoad: false
		}
	}

	render() {
		// console.log(window.innerWidth)

		return (

			<div className={"description-grid-text"}>
				<h3>
					Rocky Mountain Arsenal Database App
				</h3>
				<p>
					The Rocky Mountain Arsenal National Wildlife refuge is one of the nations largest urban wildlife
					refuges. Here I created a park web application to help guide the user around the refuge.
				</p><p>
					It is a full stack web application built with Node.js and the Express.js web application framework.
					It is powered by a PostGIS database backend and Mapbox-GL JS for the front end. Users can search for
					features and amenities in the park, and submit their own features.
				</p><p>
					For this project, OpenLayers and GeoServer were recommended. However, I decided to go above and
					beyond and tackle technologies that were new to me by building the application from the ground up.
				</p>
			</div>
		)
	}
}
