import React, {Component} from 'react';
import Button from "@material/react-button";
import {Link} from "react-router-dom";
import logoRow from '../img/logo_row.svg'
import headshot from '../img/headshot_b.jpg'
import resume from "../resume.json"

import {IoLogoLinkedin} from "react-icons/io"
import {MdChevronLeft, MdEmail, MdPhone} from "react-icons/md"
import Overdrive from "react-overdrive";

export default class AboutMeScreen extends Component {
	constructor(props) {
		super(props);
		this.state = {
			statsHeight: "300px",
			didLoad: false
		}
	}

	render() {
		// console.log(window.innerWidth)
		console.log("stats Height", this.state.statsHeight);
		const footerClass = (window.innerWidth < 520 ? "footer button-container-shadow" : "button-container-bottom white button-container-shadow button-desktop-back");
		const buttonBufferDivView = (window.innerWidth < 520 ? "visible" : "hidden");
		return (
			<Overdrive duration={150} id={"AboutMe"}>
				<div className="grid-container">
					<div className={"grid-child full-grid-overlay no-shadow flex-column"}>
						<div className={"aboutMeOpen sub-grid-child"}>
							<div className={"aboutMeOpenContent sub-grid-child"}>
								<div className={"map-logo-row"}>
									<img src={logoRow} className="jvrMapLogoRow"
									     alt="Jeff Van Rees Map Logo."/>
								</div>
								<div className={"aboutMeText"}>
									<div className={'headshot-cnt'}>
										<img className={"headshot "} alt={"Jeff's Headshot."} src={headshot}/>
									</div>
									<h3 className={"aboutMeSubTitles"}>About</h3>
									{
										`Hello, I'm Jeff and I make maps on the internet.

I transform complex questions and data into meaningful and digestible interactive visual experiences. Let's build the future of spatial together.

I live in Fort Collins, CO with my wife and two young sons. Growing up in Estes Park, CO the walls of my room were covered in maps and I carry that love of maps with me today.

Over the course of my studies and career I have gravitated toward open source tech. Im particularly passionate about PostGIS and the Mapbox stack.

`
									}
									<div className={"contact-table"}>
										<h3 className={"aboutMeSubTitles"}>Connect</h3>
										<a className={"contact-table-cell-links"}
										   href={"tel:9709805199"}>
											<Button
												icon={<MdPhone/>}
											>
												Phone
											</Button>
										</a>
										<a className={"contact-table-cell-links"}
										   href={"mailto:jvanrees@wisc.edu"}>
											<Button
												icon={<MdEmail/>}>
												Email
											</Button>
										</a>
										<a className={"contact-table-cell-links"}
										   href={"https://www.linkedin.com/in/jeffvanrees/"}>
											<Button
												icon={<IoLogoLinkedin/>}
											>
												LinkedIn
											</Button>
										</a>
									</div>
								</div>
							</div>

						</div>
						<div className={"aboutMeResume sub-grid-child"}>
							<div className={"aboutMeStatsRow"}>
								<div className={"aboutMeStatsContent"}>
									<h4 className={"aboutMeSubTitles"}>Education</h4>
									<h5 className={"aboutMeSubTitlesConnect"}>{Object.keys(resume.education)[0]}</h5>
									<h6 className={"aboutMeSubTitlesConnect"}>
										<b>{resume.education[Object.keys(resume.education)[0]].degree}</b></h6>
									<br/>
									<h5 className={"aboutMeSubTitlesConnect"}>{Object.keys(resume.education)[1]}</h5>
									<h6 className={"aboutMeSubTitlesConnect"}>{resume.education[Object.keys(resume.education)[1]].degree}</h6>
								</div>
								<div className={"aboutMeStatsContent"}>
									<h4 className={"aboutMeSubTitles"}>Experience</h4>
									<h5 className={"aboutMeSubTitlesConnect"}>Verizon Location Services (MapQuest)</h5>
									<h6 className={"aboutMeSubTitlesConnect"}>Cartographer</h6>
									<br/>
									<h5 className={"aboutMeSubTitlesConnect"}>Center for Environmental Management of
										Military Lands</h5>
									<h6 className={"aboutMeSubTitlesConnect"}>Data Technician</h6>
								</div>
								<div className={"aboutMeStatsContent"}>
									<h5 className={"aboutMeSubTitlesConnect"}>Likes</h5>
									<h6 className={"aboutMeSubTitlesConnect"}>Bunnies, FOSS, Mapbox, Deck.GL, QGIS,
										PostGIS</h6>
									<br/>
									<h5 className={"aboutMeSubTitlesConnect"}>Dislikes</h5>
									<h6 className={"aboutMeSubTitlesConnect"}>Wasps, Shapefiles, Front Lawns, esri</h6>
								</div>
							</div>

						</div>
						{window.innerWidth < 520 ?
							<div style={{visibility: buttonBufferDivView, padding: '0.5rem'}}>
								<Button>
									&nbsp;
								</Button>
							</div> : null
						}
						<div className={footerClass}>
							<Link to="/">
								<Button icon={<MdChevronLeft/>}>
									Back
								</Button>
							</Link>
							<Button
								onClick={() => window.open('https://jvanrees.github.io/docs/Van_Rees_Resume_201806_updated2.pdf', "_blank")}>
								Resume
							</Button>
						</div>
					</div>
				</div>
			</Overdrive>
		)
	}
}
