import React, {Component} from 'react';
import Button from '@material/react-button';
import AboutMe from './AboutMe.js'
import {Link} from "react-router-dom";


import Overdrive from 'react-overdrive'

export default class HomeGrid extends Component {
	constructor(props) {
		super(props);
		this.state = {
			home: true,
			aboutMe: false,
			rmaHistoryBox: false,
			googleMapsApiBox: false,
			rmaVisitorBox: false,
			qingMapBox: false,
			nightModeBox: false,
			shadedReliefBox: false,
			leafletBox: false,
			hoverInfo: null
		};

		this.emptyState = this.emptyState.bind(this)
	}

	emptyState = (e) => {
		console.log(e);
		this.setState({
			home: true,
			aboutMe: false,
			rmaHistoryBox: false,
			googleMapsApiBox: false,
			rmaVisitorBox: false,
			qingMapBox: false,
			nightModeBox: false,
			shadedReliefBox: false,
			leafletBox: false,
			hoverInfo: null
		});
	};

	render() {

		const className_aboutMe = "grid-child " + (this.state.aboutMe ? " full-grid-overlay" : " span-square-large") + " titleBox no-shadow";

		return (

			<div className="grid-container">
				{this.state.home &&
				<div className={className_aboutMe}>
					<Overdrive duration={150}  id={"AboutMe"}>
						<AboutMe/>
					</Overdrive>
					<div className={"button-container-bottom white"}>
						<Link to="/aboutme">
							<Button>
								About Me
							</Button>
						</Link>
					</div>
				</div>
				}

				{this.state.home && <div className={"grid-child span-centerpiece"}>
					<Overdrive duration={150}
						className={"overdrive-div-project"} id={"RmaAndroid"}>
						<Link to="/rma-android">
							<div className={"rmaHistoryBox home-grid-project-image"}>
								<div className={"grid-title"}>
									<div className={"grid-title-text"}>Arsenal Android App</div>
								</div>
							</div>
						</Link>
					</Overdrive>
				</div>}
				{this.state.home && <div className={"grid-child span-rect-horizontal rotate-cell"}>
					<Overdrive duration={150}
						className={"overdrive-div-project"} id={"GoogleMaps"}>
						<Link to="/google-maps-api">
							<div className={"googleMapsApiBox home-grid-project-image"}>
								<div className={"grid-title"}>
									<div className={"grid-title-text w1"}>Google Maps API</div>
								</div>
							</div>
						</Link>
					</Overdrive>
				</div>}
				{this.state.home && <div className={"grid-child span-square-small"}>
					<Overdrive duration={150}
						className={"overdrive-div-project"} id={"RmaVisitor"}>
						<Link to="/rma-post-gis">
							<div className={"rmaVisitorBox home-grid-project-image"}>
								<div className={"grid-title"}>
									<div className={"grid-title-text w1"}>PostGIS WebApp</div>
								</div>
							</div>
						</Link>
					</Overdrive>
				</div>}
				{this.state.home && <div className={"grid-child span-rect-vertical"}>
					<Overdrive duration={150}
						className={"overdrive-div-project"} id={"CartoCSS"}>
						<Link to="/carto-css">
							<div className={"qingMapBox home-grid-project-image"}>
								<div className={"grid-title"}>
									<div className={"grid-title-text w1"}>Qing Dynasty</div>
								</div>
							</div>
						</Link>
					</Overdrive>
				</div>}

				{this.state.home && <div className={"grid-child span-square-large"}>
					<Overdrive duration={175}
						className={"overdrive-div-project"} id={"NightMode"}>
						<Link to="/night-navigation">
							<div className={"nightModeBox home-grid-project-image"}>
								<div className={"grid-title"}>
									<div className={"grid-title-text"}>MapQuest Night Mode</div>
								</div>
							</div>
						</Link>
					</Overdrive>
				</div>}
				{this.state.home && <div className={"grid-child span-shadedReliefBox shadedReliefBox"}>
					<Overdrive duration={150}
						className={"overdrive-div-project"} id={"ShadedRelief"}>
						<Link to="/shaded-relief">
							<div className={"shadedReliefBox home-grid-project-image"}>
								<div className={"grid-title"}>
									<div className={"grid-title-text w1"}>Shaded Relief</div>
								</div>
							</div>
						</Link>
					</Overdrive>
				</div>}

				{this.state.home && <div className={"grid-child span-square-small expandable-cell"}>
					<Overdrive duration={150}
						className={"overdrive-div-project"} id={"LeafletMap"}>
						<Link to="/leaflet-map">
							<div className={"leafletBox home-grid-project-image "}>
								<div className={"grid-title"}>
									<div className={"grid-title-text w1"}>Early Work</div>
								</div>
							</div>
						</Link>
					</Overdrive>
				</div>}

			</div>

		)
	}
}

