import React, {Component} from 'react';

import Slider from "react-slick";

import "react-image-gallery/styles/css/image-gallery.css";

import img1 from "../../img/shaded_relief/shaded_relief_cropped.png";
import img2 from "../../img/shaded_relief/shaded_dem.png";
import img3 from "../../img/shaded_relief/shaded_dom_classification.png";
import img4 from "../../img/shaded_relief/finished_bike_map.png";


export class ShadedReliefMedia extends Component {
	constructor(props) {
		super(props);
		this.state = {
			statsHeight: "300px",
			didLoad: false
		}
	}


	render() {
		console.log()
		const sliderCenterPercent = (window.innerWidth < 720 ? "10%" : "20%")
		const settings = {
			infinite: true,
			speed: 750,
			slidesToShow: 1,
			slidesToScroll: 1,
			centerMode: true,
			centerPadding: sliderCenterPercent,
			autoplay: true,
			autoplaySpeed: 5000,
			focusOnSelect: true,
			arrows: false,
		};

		return (
			<div className={"image-carousel-container"}>
				<Slider {...settings}>
					<div className={"img-android-container"}><img src={img1} className={"img-android-original"}
					                                              alt={"Pretty shaded relief."}/>
					</div>
					<div className={"img-android-container"}><img src={img2} className={"img-android-original"}
					                                              alt={"DEM elevation data."}/>
					</div>
					<div className={"img-android-container"}><img src={img3} className={"img-android-original"}
					                                              alt={"Land coverage, used for colors in relief."}/>
					</div>
					<div className={"img-android-container"}><img src={img4} className={"img-android-original"}
					                                              alt={"The end product, a ridiculous bike race around Colorado."}/>
					</div>
				</Slider>
			</div>
		)
	}
}

export class ShadedReliefDesc extends Component {
	constructor(props) {
		super(props);
		this.state = {
			statsHeight: "300px",
			didLoad: false
		}
	}

	render() {
		// console.log(window.innerWidth)

		return (

			<div className={"description-grid-text"}>
				<h3>
					Shaded Relief
				</h3>
				<p>
					Cartography is a blend of art and information communication. The classic shaded relief, pioneered by
					Eduard Imhof, is an exemplary example of this.
					<br/><br/>
					Here, I used GIS tools, Photoshop, and Illustrator to create my take on a classic. The end result
					was a map for a hypothetical bike race that would tour our beautiful state.

				</p>
			</div>
		)
	}
}
