import React, {Component} from 'react';
import Button from "@material/react-button";
import {Link} from "react-router-dom";
import {CartoCSSDesc, CartoCSSMedia} from "./project_screens/QingDynasty"
import {RmaHistoryDesc, RmaHistoryMedia} from './project_screens/RmaHistory'
import {GoogleMapsDesc, GoogleMapsMedia} from './project_screens/GoogleMapsApi'
import {RmaVisitorDesc, RmaVisitorMedia} from './project_screens/RmaVisitor'
import {NightModeDesc, NightModeMedia} from './project_screens/NightMode'
import {ShadedReliefDesc, ShadedReliefMedia} from './project_screens/ShadedRelief'
import {LeafletDesc, LeafletMedia} from './project_screens/LeafletMap'

import {MdChevronLeft} from "react-icons/md"
import Overdrive from "react-overdrive";

export default class ProjectScreen extends Component {
	constructor(props) {
		super(props);
		this.state = {
			statsHeight: "300px",
			didLoad: false
		}
	}

	render() {

		const projectGridClass = (window.innerWidth < 530 ? "grid-container-project-mobile" : "grid-container-project");
		const descriptionGridClassName = "description-grid sub-grid-child " + this.props.project + "Desc"
		const footerClass = (window.innerWidth < 530 ? "bottom-nav footer button-container-shadow "+descriptionGridClassName : "bottom-nav button-container-bottom white button-container-shadow "+descriptionGridClassName+" button-desktop-back");
		const backButtonClassname = this.props.project+"Button"

		return (
			<Overdrive duration={200} id={this.props.project}>
				<div className="grid-container">
					<div className={"full-grid-overlay " + projectGridClass + " no-shadow"}>
						<div className={"image-grid sub-grid-child"}>
							{this.props.project === "CartoCSS"     ? <CartoCSSMedia/> : null}
							{this.props.project === "RmaAndroid"   ? <RmaHistoryMedia/> : null}
							{this.props.project === "GoogleMaps"   ? <GoogleMapsMedia/> : null}
							{this.props.project === "RmaVisitor"   ? <RmaVisitorMedia/> : null}
							{this.props.project === "NightMode"    ? <NightModeMedia/> : null}
							{this.props.project === "ShadedRelief" ? <ShadedReliefMedia/> : null}
							{this.props.project === "LeafletMap"   ? <LeafletMedia/> : null}
						</div>
						<div className={descriptionGridClassName}>
							{this.props.project === "CartoCSS"     ? <CartoCSSDesc/> : null}
							{this.props.project === "RmaAndroid"   ? <RmaHistoryDesc/> : null}
							{this.props.project === "GoogleMaps"   ? <GoogleMapsDesc/> : null}
							{this.props.project === "RmaVisitor"   ? <RmaVisitorDesc/> : null}
							{this.props.project === "NightMode"    ? <NightModeDesc/> : null}
							{this.props.project === "ShadedRelief" ? <ShadedReliefDesc/> : null}
							{this.props.project === "LeafletMap"   ? <LeafletDesc/> : null}

							{window.innerWidth <530 ?
							<div style={{padding: '0.5rem'}}>
								<Button>
									&nbsp;
								</Button>
							</div> : null}
						</div>
						<div className={footerClass}>
							<Link to="/">
								<Button icon={<MdChevronLeft/>} className={backButtonClassname}>
									Back
								</Button>
							</Link>
						</div>
					</div>
				</div>
			</Overdrive>
		)
	}
}
