import React, {Component} from 'react';

import {Map, TileLayer} from 'react-leaflet'

const center = [26.51, 80.95];

export class CartoCSSMedia extends Component {
	constructor(props) {
		super(props);
		this.state = {
			statsHeight: "300px",
			didLoad: false
		}
	}

	render() {
		// console.log(window.innerWidth)

		return (

			<Map center={center} zoom={4} minZoom={4} maxZoom={4} attributionControl={false} zoomControl={false}
			     zoomAnimation={false} fadeAnimation={false} touchZoom={false}>
				<TileLayer
					url={"https://b.tiles.mapbox.com/v4/jvanrees.9febd01c/{z}/{x}/{y}@2x.png?access_token=pk.eyJ1IjoianZhbnJlZXMiLCJhIjoiY2lzZGRvcmRhMDA0dTJ5cnRrbGRld3l0dCJ9.tYSjHXsUNgl5T6C_mOVyxQ"}
					detectRetina={true}
					minZoom={4} maxZoom={5}
				/>
			</Map>
		)
	}
}

export class CartoCSSDesc extends Component {
	constructor(props) {
		super(props);
		this.state = {
			statsHeight: "300px",
			didLoad: false
		}
	}

	render() {
		// console.log(window.innerWidth)

		return (
			<div className={"description-grid-text"}>
				<h3>
					CartoCSS Qing Dynasty Slippy Map
				</h3>
				<p>Old maps are beautiful. For my first ever webmap I was tasked to design a slippy map
					using
					CartoCSS in Mapbox Studio Classic. Desiring to create something unique, I decided to recreate the
					aesthetics of a Qing Dynasty water color map into a webmap. What drew me to this map was the beauty
					of how
					the cartographer displayed the water and the mountain ranges.

					<br/><br/>What I learned most from this
						project was
						the importance of zoom level and visualization. Different zoom levels require different levels
						of detail,
						and different image sizes. As it was my very first map created to be an artistic piece and an
						homage to an
						inspirational piece of cartographic art, I've decided to rework this one. If you love old maps,
						check out
						the original Qing dynasty era map <a
							href="http://theme.npm.edu.tw/exh105/GreenBorderlands/ch/page-2.html">here.</a>
				</p>
			</div>

		)
	}
}
