import React, {Component} from 'react';

import Iframe from 'react-iframe';

export class LeafletMedia extends Component {
	constructor(props) {
		super(props);
		this.state = {
			statsHeight: "300px",
			didLoad: false
		}
	}

	render() {
		// console.log(window.innerWidth)

		return (

				<Iframe url="https://s3.amazonaws.com/jvr-portfolio-examples/VanReesLab1/index.html"
				        id="myId"
				        className="rma-db-iframe"
				        display="initial"
				        position="relative"
				        allowFullScreen/>
		)
	}
}

export class LeafletDesc extends Component {
	constructor(props) {
		super(props);
		this.state = {
			statsHeight: "300px",
			didLoad: false
		}
	}

	render() {
		// console.log(window.innerWidth)

		return (

			<div className={"description-grid-text"}>
				<h3>
					Leaflet Webmap
				</h3>
				<p>
					Displaying spatiotemporal data has been a challenge for cartographers, but with interactive mapping
					temporal data can be communicated more clearly than ever. Here a jQuery slider is used to change
					years of data. Proportional symbols calculated on the fly to show spatial differences.
				</p>
			</div>
		)
	}
}
