import React, {Component} from 'react';

import Slider from "react-slick";

import "react-image-gallery/styles/css/image-gallery.css";

import img1 from "../../img/night_mode/Screenshot_20181130-023817.png";
import img2 from "../../img/night_mode/Screenshot_20181130-023540.png";
import img3 from "../../img/night_mode/Screenshot_20181130-023613.png";
import img4 from "../../img/night_mode/Screenshot_20181130-023746.png";
import img5 from "../../img/night_mode/Screenshot_20181130-023757.png";
import img6 from "../../img/night_mode/Screenshot_20181130-023810.png";


export class NightModeMedia extends Component {
	constructor(props) {
		super(props);
		this.state = {
			statsHeight: "300px",
			didLoad: false
		}
	}

	render() {
		console.log()
		const sliderCenterPercent = (window.innerWidth < 720 ? "20%" : "30%")
		const settings = {
			infinite: true,
			speed: 750,
			slidesToShow: 1,
			slidesToScroll: 1,
			centerMode: true,
			centerPadding: sliderCenterPercent,
			autoplay: true,
			autoplaySpeed: 5000,
			focusOnSelect: true,
			arrows: false,
		};

		return (
			<div className={"image-carousel-container"}>
				<Slider {...settings}>
					<div className={"img-android-container"}><img src={img1} className={"img-android-original"}
					                                              alt={"MapQuest Night Mode (Unreleased)"}/>
					</div>
					<div className={"img-android-container"}><img src={img2} className={"img-android-original"}
					                                              alt={"MapQuest Night Mode (Unreleased)"}/>
					</div>
					<div className={"img-android-container"}><img src={img3} className={"img-android-original"}
					                                              alt={"MapQuest Night Mode (Unreleased)"}/>
					</div>
					<div className={"img-android-container"}><img src={img4} className={"img-android-original"}
					                                              alt={"MapQuest Night Mode (Unreleased)"}/>
					</div>
					<div className={"img-android-container"}><img src={img5} className={"img-android-original"}
					                                              alt={"MapQuest Night Mode (Unreleased)"}/>
					</div>
					<div className={"img-android-container"}><img src={img6} className={"img-android-original"}
					                                              alt={"MapQuest Night Mode (Unreleased)"}/>
					</div>

				</Slider>
			</div>
		)
	}
}

export class NightModeDesc extends Component {
	constructor(props) {
		super(props);
		this.state = {
			statsHeight: "300px",
			didLoad: false
		}
	}

	render() {
		// console.log(window.innerWidth)

		return (

			<div className={"description-grid-text"}>
				<h3>
					New MapQuest Night Navigation Mode
				</h3>
				<p>
					Designed for night navigation, MapQuests new night map style provides both an aesthetically
					pleasing form and functional design. The needs of the user were paramount in the design process
					which lead us to a simplified and focused experience.
					<br/><br/>
					The process began with a competitive analysis to highlight areas of improvement in our current night
					style. In addition, harmony between our current day style and new night mode was prioritized for
					smooth transitions and consistent branding between map styles. However, the complexity of the map
					was reduced compared to the day map to reduce distractions during night navigation.

				</p>
			</div>
		)
	}
}
