import React, {Component} from 'react';

export default class OpacityController extends Component {
	constructor(props) {
		super(props);
		this.state = {
			currentCount: 1
		}
	}

	timer() {
		this.setState({
			currentCount: this.state.currentCount - 0.01
		})
		if (this.state.currentCount <= 0) {
			clearInterval(this.intervalId);
			this.setState({
				currentCount: 0
			})
		}
	}



	componentDidMount() {
		this.intervalId = setInterval(this.timer.bind(this), 10);
	}

	componentWillUnmount() {
		clearInterval(this.intervalId);
	}

	render() {
		// console.log(window.innerWidth)

		return (

			<div className={"opacity-test"} style={{fontSize: "40", textAlign: "center"}}>
				<div>{this.state.currentCount}</div>
			</div>
		)
	}
}
